body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body,
body * {
    box-sizing: border-box;
}

.page-tube .item {
    border-bottom: 2px solid #f2f2f2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 30px;
}

.page-tube .content {
    margin: 0 50px;
}

.page-tube .video-type {
    font-size: 19px;
    line-height: 24px;
    font-weight: bold;
    margin: 15px 0;
    text-align: left;
}

.page-tube .video-type .music-icon {
    background: #333;
    border-radius: 50%;
    color: #fff;
    margin-right: 8px;
    padding: 7px 10px 7px 10px;
}

.categoryitem {
    padding: 15px 0;
    border-bottom: 1px solid #ccc;
}

.categoryitem .catcont {
    display: -webkit-flex;
    display: flex;
}

.categoryitem .catcont .play-item {
    margin: 5px;
    width: 225px;
}

.categoryitem .catcont .play-item .headerinfo .image {
    width: 100%;
    overflow: hidden;
    height: 125px;
}

.categoryitem .catcont .play-item .headerinfo .image img {
    width: 100%;
}
.play-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-bottom: 30px;
    margin-right: 5px;
    position: relative;
}

.play-item .headerinfo {
    position: relative;
}

.play-item .video-item {
    border: 1px solid #aaa;
    float: left;
    height: 125px;
    width: 225px;
}

.play-item .video-info {
    float: left;
    margin-top: 12px;
    text-align: left;
    width: 225px;
}

.play-item .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0, 0, 0, 0.6);
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: none;
}

.play-item .overlay svg {
    width: 40px;
    height: 40px;
    fill: rgba(256, 256, 256, 0.9);
}

.play-item .video-info .desc {
    font-weight: bold;
    height: 45px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.play-item .video-info .info {
    color: #808080;
    font-size: 15px;
    font-weight: 600;
    margin-top: 5px;
}

.play-item .title {
    font-weight: 700;
}

.play-item .view-count {
    margin-right: 20px;
}

.play-item .video-info .info .view .view-count {
    margin-right: 5px;
}

.play-item .video-info .info .title .tick-icon {
    background: #333;
    border-radius: 50%;
    color: #fff;
    padding: 5px 7px 5px 7px;
}

.play-item:hover .overlay {
    display: -webkit-flex;
    display: flex;
}

.video-player {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}

.video-player > .overlay {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto;
	background-color: rgba(0, 0, 0, 0.7);
}

.video-player > .content {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	min-height: 100%;
}

.header {
    background-color: #333;
    display: -webkit-flex;
    display: flex;
    height: 70px;
}

.header .leftcont {
    padding-left: 50px;
    padding-top: 20px;
    text-align: left;
    width: 70%;
}

.header .leftcont .title {
    background: linear-gradient(#0000ff, #6666ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 20px;
    font-weight: 700;
}

.header .leftcont .bars-icon {
    color: #fff;
    margin-right: 23px;
}

.header .rightcont {
    padding-right: 50px;
    padding-top: 20px;
    text-align: right;
    width: 30%;
}

.header .rightcont .search-icon {
    color: #fff;
    margin-right: 30px;
}

.header .rightcont .notification-icon {
    color: #fff;
    margin-right: 18px;
}

.header .rightcont .globe-icon {
    color: #fff;
}

.header .rightcont .down-icon {
    color: #fff;
}

.footer {
    height: 110px;
    background-color: #fff;
    box-shadow: 2px -2px 4px -3px #777;
    margin-top: 50px;
    color: #a6a6a6;
    font-size: 12px;
    padding-top: 20px;
    overflow: hidden;
}

.footer .topcont {
    width: 100%;
    height: 40px;
    border-bottom: 2px solid #e6e6e6;
    display: -webkit-flex;
    display: flex;
    padding: 0 50px;
}

.footer .topcont .leftcont {
    text-align: left;
}

.footer .topcont .rightcont {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: right;    
    -webkit-align-self: flex-end;    
            align-self: flex-end;
}

.footer .topcont .rightcont span {
    font-size: 12px;
    line-height: 38px;
    display: inline-block;
    padding-bottom: 7px;
    -webkit-align-self: right;
            align-self: right;
    margin-left: 15px;
} 

.footer .topcont .leftcont .title {
    color: #cccccc;
    font-family: monospace;
    font-size: 20px;
    margin-right: 13px;
}

.footer .topcont .rightcont span {
    margin-right: 22px;
}

.footer .bottomcont {
    margin-top: 10px;
    padding: 0 50px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
}

.footer .bottomcont .left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.footer-item {
    background-color: #000;
    border: none;
    border-radius: 3px;
    color: #fff;
    height: 25px;
    margin-right: 12px;
    padding: 2px 7px 2px 7px;
    width: 90px;
}

.footer-item .leftcont {
    text-align: left;
    width: 30px;
}

.footer-item .rightcont {
    float: right;
    text-align: left;
    width: 60px;
}

.footer-item .rightcont .subtitle{
    font-size: 7px;
}

.footer-item .rightcont .title{
    font-size: 11px;
}

